#updateData{
    position:fixed;
    width: 100%;
    height: 50px;
    top: 55px;
    border-bottom: 1px solid #ccc;
    background: white;
    display:flex;
    align-items: center;
    z-index: 0;
}
#update_data_container{
    width: 90%;
    display:flex;
    margin: 0 auto;
    align-items: center;
    justify-content: space-between;
}
#user{
    display: flex;
    align-items: center;
    background: #eee;
    height: 25px;
    padding: 0px 8px;
    gap: 5px;
    border: 1px solid #ccc;
    border-radius: 3px;
}
#user:hover{
    cursor: pointer;
}
#user_profile{
    width: 10px;
    height: 10px;
    background:gold;
    padding: 2px;
    border-radius: 50%;
}

#addDataBtn{
    font-size: 14px;
    width: 100px;
    height: 30px;
    border: 1px solid #34ace0;
    border-radius: 3px;
    background: #227093;
    color: white;
    
}
#addDataBtn:hover{
    cursor: pointer;
    background: #34ace0;
}