

*:focus{
    outline: none;
    
}

a{
    color:#474787;
}
button{
    font-size: 14px;
    font-weight: 500;
    width: 100px;
    height: 30px;
    border-radius: 2px;
    color: white;
}
select:hover{
    cursor: pointer;
}

.folder{
    display: inline;
    margin-left: 20px;
    text-decoration: underline;
}
.folder:nth-of-type(1){
    margin-left: 0;
}
.folder:hover{
    cursor: pointer;
}

/* split this */
.modal-position{
    position:fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0,0,0,0.6);
    z-index: 100;
}
.modal_title{
    color: black;
}
.modal_container{
    padding: 10px;
    width: 90%;
    display: block;
    margin: 0 auto;
}
