
#rule_header{
    display: flex;
    align-items: center;
}
#add_rule{
    border: 1px solid #5f5fb3;
    background: #474787;
    margin-left: 10px;
}

#add_rule:hover{
    cursor: pointer;
    background: #5f5fb3;
}
#rules h2{
    font-size: 20px;
    font-weight: 500;
}

.rule{
    width: 100%;
    background: #eee;
    border: 1px solid #ccc;
    border-radius: 2px;
    padding: 5px;
    margin-top: 10px;
}

.run_rule{
    border: 1px solid #6ab04c;
    background: #447330;
}

.run_rule:hover{
    cursor: pointer;
    background: #6ab04c;
}

.remove_rule{
    border: 1px solid #f5613d;
    background: #c23616;
    float:right;
}

.remove_rule:hover{
    cursor: pointer;
    background: #f5613d;
}

.success{
    background: #447330;
    color: white;
}

.fail{
    background: #c23616;
}