

#main{
    padding-top: 110px;
    width: 90%;
    display:block;
    margin: 0 auto;
}

#main_title_wrapper{
    height: 70px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #ccc;
}
#main_title{
    font-size: 40px;
    
}
#num_docs{
    margin-left: 10px;
    padding-top: 5px;
}

#add_folder{
    border: 1px solid #5f5fb3;
    background: #474787;
    margin-left: 10px;
    margin-top: 5px;
}

#add_folder:hover{
    cursor: pointer;
    background: #5f5fb3;
}

#data_container{
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    margin-top: 20px;
}
